.todoitem {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  border: 1px solid #e7e7e7;
  border-radius: 4px;
}

.todoitem:hover {
  background-color: #e7e7e7;
}

.todoitem__text {
  flex: 3;
  padding: 5px;
}
.todoitem__text--line {
  text-decoration: line-through;
}
.todoitem__checkbox {
  flex: 1;
}
.todoitem__button {
  background-color: white;
  color: black;
  border: 2px solid #f44336;
  flex: 1;
  padding: 1px 1px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border-radius: 4px;
  font-size: 16px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
}
.todoitem__button:hover {
  background-color: #f44336;
  color: white;
}
